import React, { useState } from 'react';
import {
  Button,
  Container,
  TextField,
  Typography,
  Alert,
  Paper
} from '@mui/material';
import { submitSecret } from '../api';

export default function CreateSecret() {
  const [secret, setSecret] = useState('');
  const [link, setLink] = useState('');
  const [error, setError] = useState('');

  const handleSubmit = async () => {
    setError('');
    setLink('');
    try {
      const res = await submitSecret(secret);
      setLink(`${window.location.origin}${res.link}`);
      setSecret('');
    } catch (err) {
      setError('Failed to submit secret');
    }
  };

  return (
    <Container maxWidth="sm">
      <Paper elevation={4} sx={{ padding: 4, marginTop: 10 }}>
        <Typography variant="h5" gutterBottom>
          One-Time Secret Generator
        </Typography>

        <TextField
          label="Enter your secret"
          fullWidth
          multiline
          rows={4}
          margin="normal"
          value={secret}
          onChange={(e) => setSecret(e.target.value)}
        />

        <Button variant="contained" fullWidth onClick={handleSubmit}>
          Generate Link
        </Button>

        {link && (
          <Alert severity="success" sx={{ mt: 2 }}>
            Share this link:&nbsp;
            <a
              href={link}
              target="_blank"
              rel="noopener noreferrer"
              style={{ color: 'inherit', textDecoration: 'underline', wordBreak: 'break-all' }}
            >
              {link}
            </a>
          </Alert>
        )}

        {error && (
          <Alert severity="error" sx={{ mt: 2 }}>
            {error}
          </Alert>
        )}
      </Paper>
    </Container>
  );
}
